<template>
  <div style="margin: .2rem;">
    <v-card dark v-if="imgUrl!==''||textDescription.length>0" outlined color="rgba(83,83,86,0.4)">
      <div class="list-card">
        <div :style="{width: avatarWidth+'px',height: avatarWidth+'px',background: 'rgba(83,83,86,0.3)'}"
             class="avatar-img">
          <img v-if="imgUrl!==''" :src="imgUrl" alt="暂无图片" @click="showInfo"
               :style="{width: avatarWidth+'px',height: avatarWidth+'px', objectFit: 'cover', display: 'block'}"/>
        </div>
        <div style="width: 100%;margin-left: .2rem;">
          <p v-if="title" class="text-h5" style="margin-top: 1rem">{{ title }}</p>
          <div v-if="textDescription.length>0" v-for="(text,i) in textDescription" :key="i">
            <p style="width: 100%;word-wrap:break-word;word-break:break-all">{{ text }}</p>
          </div>
          <v-card-text class="btn-box">
            <v-btn outlined rounded @click="showInfo">查看详情</v-btn>
          </v-card-text>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "ListCard",
  props: {
    imgUrl: {
      type: String,
      default: ""
    },
    textDescription: {
      type: Array,
      default: () => {
        return [];
      }
    },
    title: {
      type: String,
      default: '',
    },
    id: {
      required: true
    }
  },
  data: () => ({
    avatarWidth: 150
  }),
  methods: {
    showInfo() {
      console.log(this.id)
      this.$router.push({
        path: '/Detail',
        query: {id: this.id}
      })
    }
  },
  created() {
    this.avatarWidth = this.$computeWidth(0.4);
  },
  watch: {
    '$store.state.windowWidth'(newVal) {
      this.avatarWidth = this.$computeWidth(0.4);
    }
  },
};
</script>

